<template>
  <div class="admin">
    <!-- <left-sidebar></left-sidebar> -->
    <!-- <right-wrap></right-wrap> -->
    <div class="menus">
        <div class="menus-head">
          <h4 class="title">机构列表</h4>
          <!-- <a href="javascript:;" class="add-btn">✚ 新建</a> -->
        </div>
        <div class="seach-bar">
          <el-input
            placeholder="请输入机构名称"
            icon="search"
            v-model="searchText">
          </el-input>
          <!-- <el-input v-model=""></el-input> -->
        </div>
        <!-- @open="handleOpen" @close="handleClose" -->
        <!-- <el-menu default-active="1" class="el-menu-vertical-demo" >
          <el-menu-item index="1"><i class="el-icon-menu"></i>北京</el-menu-item>
          <el-menu-item index="2"><i class="el-icon-menu"></i>北京</el-menu-item>
          <el-menu-item index="3"><i class="el-icon-setting"></i>上海</el-menu-item>
        </el-menu> -->
        <el-tree
          :data="orgListsData"
          :props="defaultProps"
          accordion
          :filter-node-method="filterNode"
          @node-click="getNodeId"
          ref="orgLists"
          >
        </el-tree>
    </div>
    <div class="main-content" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="页面加载中...">
      <div class="tabs">
        <!-- type="border-card" -->
        <el-tabs >
          <el-tab-pane label="机构信息">
              <div class="tabs-header text-right">
                <el-button type="success" icon="edit" @click="editOrgInfoForm" v-if="!isEditing">编辑</el-button>
                <el-button type="success" icon="edit" @click="cancelOrgInfoForm" v-if="isEditing">取消</el-button>
                <el-button type="success" icon="edit" @click="saveOrgInfoForm" v-if="isEditing">保存</el-button>
              </div>
              <div class="tabs-body" >
                <el-form ref="form" :model="orgInfoForm" :rules="orgInfoRules" label-width="100px" class="edit" :class="[{ editing: isEditing }] " >
                  <el-form-item label="机构名称：" prop="childrenName">
                    <el-input v-model="orgInfoForm.childrenName" class="item-text"></el-input>
                    <p> {{ orgInfoForm.childrenName }} </p>
                  </el-form-item>
                  <el-form-item label="所在地址：" prop="address">

                    <el-select v-model="selectedProvince" placeholder="请选择省" @change="provinceChange" class="item-text">
                        <el-option
                          v-for="item in province"
                          :label="item.name"
                          :value="item.id"
                          :key="item.id"
                          >
                        </el-option>
                      </el-select>


                    <el-select v-model="selectedCity" @change="cityChange" placeholder="请选择市" class="item-text" v-if="city.length !=0 ">
                        <el-option
                          v-for="item in city"
                          :label="item.name"
                          :value="item.id"
                          :key="item.id"
                          >
                        </el-option>
                    </el-select>

                    <el-select v-model="selectedCounty" @change="countyChange" placeholder="请选择区/县" class="item-text" v-if="county.length !=0 ">
                      <el-option
                        v-for="item in county"
                        :label="item.name"
                        :value="item.id"
                        :key="item.id"
                        >
                      </el-option>
                    </el-select>
                    <div class="item-text">
                      <el-input type="textarea" v-model="orgInfoForm.address" class=""></el-input>
                    </div>

                    <p> {{ orgInfoForm.address }} </p>
                  </el-form-item>
                  <el-form-item label="学校数量：" prop="schoolNum">
                    <span> {{ orgInfoForm.schoolNum }} </span> <a href="javascript:;" >学校列表</a>
                  </el-form-item>
                  <el-form-item label="描述：" prop="note">
                    <el-input type="textarea" v-model="orgInfoForm.note" prop="note" class="item-text"></el-input>
                    <p> {{ orgInfoForm.note }} </p>
                  </el-form-item>
                </el-form>
              </div>
          </el-tab-pane>

        </el-tabs>
        <el-tabs>
          <el-tab-pane label="机构人员">
            <div class="tabs-header space-between">
              <el-button type="success" @click="addOrgUser" icon="plus">新建人员</el-button>
              <!-- <el-button type="danger" @click="orgUsersBatchDelete" icon="delete2" :disabled="true">批量删除</el-button> -->
            </div>
            <div class="tabs-body">
              <el-table
                  :data="orgUsers"
                  border
                  tooltip-effect="dark"
                  @cell-click="getOrgUserCurrentInfo"
                  @selection-change="orgUsersSelectionChange">
                  <!-- <el-table-column
                    type="selection"
                    width="55">
                  </el-table-column> -->
                  <el-table-column
                    prop="name"
                    label="姓名"
                    width="120">
                  </el-table-column>
                  <el-table-column
                    prop="phone"
                    label="手机号"
                    width="150">

                  </el-table-column>

                  <el-table-column
                    prop="state"
                    label="状态"
                    width="150"
                    >
                    <template scope="scope">
                      <el-dropdown trigger="click" @command="getOrgUserSelectState">
                            <span class="el-dropdown-link">
                              <b v-if="scope.row.state == 1">正常</b>
                              <b v-else>禁用</b>
                              <i class="el-icon-caret-bottom el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                              <el-dropdown-item v-if="scope.row.state == 1" command="0">禁用</el-dropdown-item>
                              <el-dropdown-item v-if="scope.row.state == 0" command="1">正常</el-dropdown-item>
                            </el-dropdown-menu>
                          </el-dropdown>
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="操作"
                    width="320"
                    >

                    <template scope="scope">
                        <el-button
                          type="primary"
                          size="small"
                          @click="orgUsersEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                          type="primary"
                          size="small"
                          @click="orgUsersRestPwd(scope.$index, scope.row)">重置密码</el-button>
                        <el-button
                          type="danger"
                          size="small"
                          @click="orgUsersDelete(scope.$index, scope.row)">删除</el-button>
                      </template>
                    </el-table-column>
                </el-table>

                <div class="pagination text-right">
                    <!-- <el-pagination
                      :page-size="10"
                      layout="prev, pager, next, jumper"
                      :total="orgUserTotal"
                      @current-change="paginationCurrentChange"
                      >
                    </el-pagination> -->
                </div>

                <el-dialog title="新建机构人员" v-model="dialogVisibleAdd"  :close-on-click-modal="false" size="tiny" >
                  <el-form ref="orgUserFormAdd" :model="orgUserFormAdd" :rules="orgUserAddRules" label-width="90px">
                    <el-form-item label="姓名：" prop="name">
                      <el-input v-model.trim="orgUserFormAdd.name"></el-input>
                    </el-form-item>
                    <el-form-item label="手机号：" prop="phone">
                      <el-input v-model.trim="orgUserFormAdd.phone"></el-input>
                    </el-form-item>
                  </el-form>
                  <span slot="footer" class="dialog-footer">
                    <el-button type="info" @click="dialogVisibleAdd = false">取 消</el-button>
                    <el-button type="success" @click="orgUserFormAddData()">添 加</el-button>
                  </span>
                </el-dialog>

                <el-dialog title="编辑机构人员" v-model="dialogVisibleEdit"  :close-on-click-modal="false" size="tiny" >
                  <el-form ref="orgUserFormEdit" :model="orgUserFormEdit" :rules="orgUserAddRules" label-width="90px">
                    <el-form-item label="姓名：" prop="name">
                      <el-input v-model.trim="orgUserFormEdit.name"></el-input>
                    </el-form-item>
                    <el-form-item label="手机号：" prop="phone">
                      <el-input v-model.trim="orgUserFormEdit.phone"></el-input>
                    </el-form-item>
                  </el-form>
                  <span slot="footer" class="dialog-footer">
                    <el-button type="info" @click="dialogVisibleEdit = false">取 消</el-button>
                    <el-button type="success" @click="orgUserFormEditData()">保 存</el-button>
                  </span>
                </el-dialog>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
  // import leftSidebar from './leftSidebar.vue';
  // import rightWrap from './rightWrap.vue';
  export default {
    name: 'admin',
    data() {
          return {
            value:'',
            // 全屏遮罩层
            fullscreenLoading: true,
            // 编辑输入框
            orgInfoForm:{
              childrenName:'',
              address:'',
              schoolNum:'',
              note:'',
            },
            // 地区
            province:[],
            city:[],
            county:[],
            selectedProvince:'',
            selectedCity:'',
            selectedCounty:'',

            // 校验
            orgInfoRules: {
              childrenName: [
                {required: true, message: '请输入机构名称', trigger: 'blur' },
                {pattern: /^[\u4E00-\u9FA5]+$/,message: '用户名只能为中文'},
                {min: 6, max: 30, message: '长度在 6 到 30 个字符' },
              ],
              note: [
                {required: true, message: '请输入描述信息', trigger: 'change' },
                {min: 0, max: 200, message: '长度在 0 到 200 个汉字' },
              ]
            },
            // 机构列表
            orgListsData: [],
            // 文本搜索
            searchText: '',
            // 树形图参数
            defaultProps: {
              children: 'childrens',
              label: 'name'
            },
            // 机构id
            orgInfoId:'110106',
            // 选择机构id
            chooseOrgId:'',
            // 机构类型
            type:'',
            // 上级机构id
            pId:'',
            // 地址
            address:'',
            // 名称
            name:'',
            // 编辑切换
            isEditing:false,
            // 机构人员
            orgUsers: [],
            // 机构人员总数
            orgUserTotal:0,
            // 新建机构人员字段
            orgUserFormAdd:{
              name:'',
              phone:'',
            },
            // 获取当前点击行的信息
            currentInfo:'',
            // 获取修改状态信息
            orgUserStutas:'',
            // 编辑机构人员字段
            orgUserFormEdit:{
              name:'',
              phone:'',
            },
            // 机构人员字输入段验证
            orgUserAddRules: {
                name: [{
                    required: true,
                    message: '请输入姓名',
                    trigger: 'blur'
                }, {
                    pattern: /^[\u4E00-\u9FA5]+$/,
                    message: '用户名只能为中文',
                    trigger: 'blur'
                }, {
                    min: 2,
                    max: 10,
                    message: '长度在 2 到 10 个汉字',
                    trigger: 'blur'
                }],
                phone: [{
                        required: true,
                        message: '请输入手机号',
                        trigger: 'blur'
                    },{
                        pattern: /^1[34578]\d{9}$/,
                        message: '请输入11位手机号',
                        trigger: 'blur'
                    }

                ],
            },


            // 隐藏对话框
            dialogVisibleAdd: false,
            dialogVisibleEdit: false,
            //列表选中列
            multipleSelection: [],

          }
        },
    mounted(){
      this.getOrgTree()
      this.getOrgUserLists()
      this.getAreaTree()
    },
    methods: {
      // 获取节点id
      getNodeId(data) {
        // this.orgInfoId=data.id;
        console.log(this.orgInfoId)

        this.getOrgInfo(this.orgInfoId)

        this.chooseOrg()
      },
      // 获取机构信息
      getOrgInfo(id){
        // this.$ajax('/eduadmin/orgInfo',{id: id})
        this.$ajax('/eduadmin/orgInfo',{id: this.orgInfoId})

        .then(res => {
            this.orgInfoForm=res.data;

        });
      },
      // 获取机构列表
      getOrgTree(){
        this.fullscreenLoading = true;
        let params={
          userId: 1
        };
        this.$ajax('/eduadmin/getOrgTree',params)
        .then(res => {
              this.orgListsData = res.data;
              this.getOrgInfo(this.orgListsData[0].id);
              this.fullscreenLoading = false;

        });
      },

      // 实时搜索
      filterNode(value, data) {
        if (!value) return true;
        console.log(data.name)
        return data.name.indexOf(value) !== -1;
      },
      // 编辑机构信息
      editOrgInfoForm(){

        this.isEditing=true;

      },
      // 取消机构信息
      cancelOrgInfoForm(){

        this.isEditing=false;
        this.getOrgInfo(this.orgInfoId)

      },
      // 获取获取区域树
      getAreaTree(){
        this.$ajax('/org/getTree')
        .then(res => {
            this.province = res.data;
        }).catch(() => {

        });
      },

      provinceChange(id){

        this.selectedProvince=id;

        let params={
          parentId: this.selectedProvince
        };

        this.$ajax('/org/getChildren',params)
        .then(res => {

            this.city = res.data;

        }).catch(() => {

        });

      },
      cityChange(id){

        this.selectedCity=id;

        let params={
          parentId: this.selectedCity
        };

        this.$ajax('/org/getChildren',params)
        .then(res => {

            this.county = res.data;

        }).catch(() => {

        });
      },
      countyChange(id){

        this.selectedCounty=id;

      },

      // 保存机构信息
      saveOrgInfoForm(){

          let params={
            id: this.orgInfoId,
            childrenName: this.orgInfoForm.childrenName,
            orgId: this.selectedCounty || this.selectedCity || this.selectedProvince,

            address: this.orgInfoForm.address,
            note: this.orgInfoForm.note
          };

          this.$ajax('/eduadmin/editOrg',params)
          .then(res => {
                if(res.code == 200){
                  this.$message({
                    type: 'success',
                    message: '保存成功!'
                  });
                  this.isEditing=false;
                }else{
                  this.$message({
                    type: 'error',
                    message: res.message
                  });
                  this.isEditing=false;
                }

          });


      },
      // 获取机构人员列表
      getOrgUserLists(){

        this.$ajax('/eduadmin/orgUserList',{orgId: this.orgInfoId})
        .then(res => {
              // console.log(res)
              this.orgUsers=res.data;


              this.orgUserTotal=this.orgUsers.length;

        });
      },
      // 新建机构人员
      addOrgUser(){
        this.orgUserFormAdd={};
        this.dialogVisibleAdd = true;

      },
      //确定添加机构人员
      orgUserFormAddData(){

        this.$refs.orgUserFormAdd.validate((valid) => {
                  if (valid) {

                    let params={
                      orgId: this.orgInfoId,
                      name: this.orgUserFormAdd.name,
                      phone: this.orgUserFormAdd.phone
                    };

                    this.$ajax('/eduadmin/addOrgUser',params)
                    .then(res => {
                          if(res.code == 200){
                            this.$message({
                              type: 'success',
                              message: '添加成功!'
                            });

                            this.dialogVisibleAdd = false;

                            this.getOrgUserLists()

                          }else{
                            this.$message({
                              type: 'error',
                              message: res.message
                            });
                            this.dialogVisibleAdd = false;
                          }

                    });

                  } else {

                    return false;

                  }
                });


      },
      // 获取点击当前行信息
      getOrgUserCurrentInfo(row, column, cell, event){
        // console.log(row.id)
        this.currentInfo = row;
      },
      // 获取机构人员状态
      getOrgUserSelectState(command){

          let params={
            orgId: this.orgInfoId,
            userId: this.currentInfo.id,
            state: command
          };

          this.$ajax('/eduadmin/changeOrgUserStatus',params).then((res) => {

            if(res.code == 200){

              this.$message({
                message: '状态修改成功',
                type: 'success'
              });

              this.getOrgUserLists()

            }
        }).catch(() => {

        });


      },
      // 编辑机构人员信息
      orgUsersEdit(index, row) {

        this.dialogVisibleEdit = true;

        this.orgUserFormEdit.name= row.name;
        this.orgUserFormEdit.phone= row.phone;

        // console.log(index, row);
      },
      // 确定编辑
      orgUserFormEditData(){


        let params = {
          orgId: this.orgInfoId,
          userId: this.currentInfo.id,
          name: this.orgUserFormEdit.name,
          phone:  this.orgUserFormEdit.phone
        };

        this.$ajax('/eduadmin/editOrgUser',params).then((res) => {

          if(res.code == 200){

            this.dialogVisibleEdit = false;

            this.$message({
              message: '保存成功',
              type: 'success'
            });

          }

          this.getOrgUserLists()

        }).catch(() => {

        });
      },
      // 重置密码
      orgUsersRestPwd(index, row){
        console.log(row)
        this.$confirm('是否要重置密码为手机号码后六位?', '提示', {
          type: 'warning',
          confirmButtonText: '重置',
          cancelButtonText: '取消',
        }).then(() => {

          let params = {
            phone: row.phone
          };

          this.$ajax('/password/reset',params).then((res) => {
            if(res.code == 200){
              this.$message({
                message: '重置成功',
                type: 'success'
              });
            }

          });
        }).catch(() => {

        });

      },
      // 确定删除单个机构人员
      orgUsersDelete(index, row) {
        console.log(index,row)
        this.$confirm('确定要删除选中教师吗?', '提示', {
          type: 'warning',
          confirmButtonText: '删除',
          cancelButtonText: '取消',
        }).then(() => {

          let params = {
            userId: row.id,
            orgId:  this.orgInfoId
          };

          this.$ajax('/eduadmin/delOrgUser',params).then((res) => {
            if(res.code == 200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.getOrgUserLists()
          });
        }).catch(() => {

        });
      },
      //获取列表选中列
      orgUsersSelectionChange(val) {
        console.log(1)
        this.multipleSelection = val;
      },
      // 批量删除机构人员 备选
      orgUsersBatchDelete(index, row){
        var userIds = this.multipleSelection.map(item => item.id).toString();

        this.$confirm('确定要删除选中教师吗?', '提示', {
          type: 'warning',
          confirmButtonText: '删除',
          cancelButtonText: '取消',
        }).then(() => {

          let params = {
            userId: userIds,
            orgId:  this.orgInfoId
          };

          this.$ajax('/eduadmin/delOrgUser',params).then((res) => {
            if(res.code == 200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.getOrgUserLists()
          });
        }).catch(() => {

        });
      },
      // 分页
      paginationCurrentChange(val){

      }
    },
    watch: {
      searchText(val) {
        this.$refs.orgLists.filter(val);
      }
    },

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .admin{
    margin-top: 15px;
    height: 100%;
    text-align: left;
    display: flex;
    justify-content: space-between;
  }
  .menus{
    width: 200px;
    border: 1px solid #20A0FF;
  }
  .menus-head{
    background: #20A0FF;
    color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
  }
  .menus-head .title{
    font-size: 14px;

  }
  .menus-head .add-btn{
    color:#fff;
    border: 1px solid #fff;
    font-size: 12px;
    padding: 2px;
  }
  .seach-bar{
    padding: 10px;
  }
  .seach-bar input{
    height: 30px;
    width: 100%;
    border: 1px solid #20A0FF;
  }
  .main-content{
    width: 800px;
  }
  /* .tabs .el-tabs--border-card>.el-tabs__header .el-tabs__item{
    border-top: 3px solid;
  }
  .tabs .el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
    border-top: 3px solid;
  } */

  .el-tabs__active-bar{
    top: 0;
  }
  .el-tree-node.is-current{
    color: #20A0FF;
  }
  .tabs-header{

    margin-bottom: 10px;
  }
  .text-left {
    text-align: left;
  }
  .text-right {
    text-align: right;
  }
  .space-between{
    display: flex;
    justify-content: space-between;
  }
  .space-around{
    display: flex;
    justify-content: space-around;
  }
  .tabs-header .editor{
    padding: 10px;
    display: block;
  }

  /* .el-form-item.is-required .el-form-item__label:before{
    content: '•';
    color: #999;
  } */

  .edit .item-text{
    display: none;
    /* display: inline-block; */
  }
  .edit.editing .item-text{
    display: block;
    width: 200px;
  }
  .edit.editing p{
    display: none;
  }
  .edit.editing .item-text.el-select{
    display: inline-block;
  }
  .edit .item-text .el-textarea{
    margin-top: 22px;
    width: 300px;
    display: block;
  }
  .pagination{
    margin-top: 30px;
  }
  b{
    font-weight: normal;
  }
  .details-address{
    margin-top: 10px;
  }
</style>



